<template>
  <div class="container">
    <!-- <h1>图片列表</h1> -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>内容管理</el-breadcrumb-item>
      <el-breadcrumb-item>图片列表</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="230"></el-table-column>
      <el-table-column prop="icon" label="图标">
        <template slot-scope="scope">
          <img :src="scope.row.icon" alt="" style="height:3rem" />
        </template>
      </el-table-column>
      <el-table-column prop="name" label="图片名称"></el-table-column>
      <el-table-column prop="icon" label="图片地址"></el-table-column>
      <el-table-column fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/items/edit/${scope.row._id}`)"
            >编辑</el-button
          >

          <el-button type="primary" size="small" @click="remove(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    }
  },
  methods: {
    //便与以后重新调用
    async fetch() {
      const res = await this.$http.get('rest/items')
      this.items = res.data
    },
    async remove(row) {
      this.$confirm(`是否要删除分类${row.name}`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(async () => {
        //点击确认按钮 既执行删除
        await this.$http.delete(`rest/items/${row._id}`)
        this.$message({
          type: 'success',
          message: '删除成功!',
        })
        //重新获取一下数据
        this.fetch()
      })
    },
  },
  created() {
    this.fetch()
  },
  components: {},
}
</script>

<style scoped lang="scss"></style>
