<template>
  <div class="home">
    <div v-if="show">
      <el-row :gutter="12" v-if="model">
        <el-col :span="8">
          <el-card shadow="always">
            <div class="card--nav">网站访问量</div>
            <div class="card--nav violet">
              <i class="el-icon-view"></i
              ><span class="fz20">{{ model.webVisits }}</span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card shadow="always">
            <div class="card--nav">网站用户量</div>
            <div class="card--nav violet">
              <i class="el-icon-user-solid"></i
              ><span class="fz20">{{ model.userVisits }}</span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card shadow="always">
            <div class="card--nav">文章数</div>
            <div class="card--nav violet">
              <i class="el-icon-collection"></i
              ><span class="fz20">{{ model.noteVisits }}</span>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <!-- echarts -->
      <div class="my_echarts">
        <v-chart autoresize :option="orgOptions"></v-chart>
      </div>
    </div>
    <!-- loading -->
    <div class="loading" v-else>
      <img src="./../assets/loading.gif" alt="" />
    </div>
  </div>
</template>

<script>
import ECharts from 'vue-echarts'
import 'echarts'

export default {
  name: 'home',
  components: {
    'v-chart': ECharts,
  },
  data: function() {
    return {
      webNum: 100,
      userNum: 1,
      noteNum: 1,
      talkNum: 1,
      orgOptions: null,
      taking: [],
      yearmonth: [],
      model: null,
      show: false,
    }
  },
  methods: {
    async fetchEchartsData() {
      const res = await this.$http.get(`rest/others`)
      const user = await this.$http.get('rest/users')
      const blogs = await this.$http.get('rest/blogs')
      this.model = res.data[0]
      this.model.userVisits = user.data.length
      this.model.noteVisits = blogs.data.length
      this.model.echartsWebVisits = this.model.echartsWebVisits
      var date = this.model.echartsWebVisits.map((v) => {
        let year = new Date(v.date).getFullYear()
        let month = new Date(v.date).getMonth() + 1
        let day = new Date(v.date).getDate()
        return year + '-' + month + '-' + day
      })
      var num = this.model.echartsWebVisits.map((v) => {
        return v.num
      })
      console.log(this.model)
      var option = {
        title: {
          text: 'blog访问量',
          subtext: '单位：人次',
        },
        tooltip: {
          show: true,
        },
        toolbox: {
          show: true,
          feature: {
            mark: { show: false },
            dataView: { show: true, readOnly: false },
            magicType: { show: true, type: ['line', 'bar'] },
            restore: { show: true },
            saveAsImage: { show: true },
          },
        },
        legend: {
          data: [],
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: [],
          axisTick: {
            show: true,
          },
        },
        yAxis: [
          {
            type: 'value',
            minInterval: 1,
            axisLabel: {
              formatter: '{value}',
            },
            boundaryGap: [0, 0],
            scale: true, // 最小值
          },
        ],
        series: [
          // y轴
          {
            name: '网站访问量',
            type: 'line',
            data: [],
            markPoint: {
              data: [
                {
                  type: 'max',
                  name: '最大值',
                },
                {
                  type: 'min',
                  name: '最小值',
                },
              ],
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: '#C4EAFC',
                },
              },
              emphasis: {
                color: '#1DBB37',
                borderColor: 'rgb(242, 7, 109)',
              },
            },
            symbolSize: 15,
            symbol: 'circle',
          },
        ],
      }
      option.xAxis.data = date
      option.series[0].data = num
      this.orgOptions = option
      this.show = true
    },
  },
  created() {
    this.fetchEchartsData()
  },
  mounted() {},
}
</script>
