.loading {
  margin: 180px auto;
  width: 128px;
  height: 128px;
  position: relative;
}
.fz20 {
  font-size: 20px;
  margin-left: 5px;
}
.my_echarts {
  margin-top: 30px;
  height: 400px;
}
.violet {
  color: rgb(219, 24, 219);
}
.card--nav {
  text-align: center;
}
</style>
