<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>内容管理</el-breadcrumb-item>
      <el-breadcrumb-item>{{ id ? '编辑' : '新建' }}文章</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <!-- 
         @submit.native.prevent="save"
         submit事件触发会执行save函数
         native.prevent为取消submit的默认事件
      -->
    <el-form label-width="120px" @submit.native.prevent="save">
      <!-- <markdown-editor v-model="content" /> -->
      <el-form-item label="标题">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="创建日期">
        <el-date-picker
          v-model="model.created_at"
          type="datetime"
          placeholder="选择日期时间"
          align="right"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>

      <el-form-item label="是否需要登入">
        <el-select v-model="model.needLoginGithub">
          <el-option
            v-for="item in needLoginGithub"
            :key="item"
            :label="item.name"
            :value="item"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="原文链接">
        <el-input v-model="model.html_url"></el-input>
      </el-form-item>
      <el-form-item label="分类">
        <el-button type="default" size="small" @click="model.labels.push({})"
          ><i class="el-icon-plus"></i>添加分类</el-button
        >
        <el-row type="flex" style="flex-wrap:wrap">
          <el-col :md="24" v-for="(item, i) in model.labels" :key="i">
            <el-form-item label="分类名称" class="mt5">
              <el-input v-model="item.name"></el-input>
            </el-form-item>
            <el-form-item label="跳转链接URL" class="mt5">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="显示颜色" style="height:40px" class="mt5">
              <el-color-picker v-model="item.color"></el-color-picker>
            </el-form-item>
            <el-form-item label="删除分类" class="mt5">
              <el-button
                type="danger"
                icon="el-icon-delete"
                circle
                @click="delete_item(i)"
              ></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item label="详情">
        <markdown-editor v-model="model.body" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import MarkdownEditor from '../components/MarkdownEditor'
export default {
  props: {
    //接受路由传递过来的值
    id: { type: String },
  },
  components: {
    MarkdownEditor,
  },
  data() {
    return {
      model: {
        labels: [],
      },
      categories: [],
      content: '',
      needLoginGithub: [true, false],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            },
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            },
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            },
          },
        ],
      },
    }
  },
  created() {
    /* 
        true  && false =>false
        false && true => false
        true  || false =>true
        false || true =>true 
     */
    this.id && this.fetch() //=> 如果this.id 为true 既存在的时候执行this.fetch函数
    this.fetchCategories()
  },
  methods: {
    delete_item(index) {
      let arr = this.model.labels
      if (this.model.labels.length > 0) {
        arr.splice(index, 1)
      } else {
        arr = []
      }
      this.model.labels = arr
      // console.log(this.model.labels)
      // this.model.labels.length > 0 &&
      //   (this.model.labels = this.model.labels.splice(index, 1))
    },
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      //=>html:new FormData()自带的表单提交数据
      //=>提交数据有两种方式：一种是提交表单数据、另一种是提交json数据
      //=>因为要上传文件所有这里要使用表单数据 既然new FormData()
      const formData = new FormData()
      //append 添加字段
      formData.append('file', file)
      //存储图片
      const res = await this.$http.post('upload', formData)
      //将存储的图片url发送到图标所在的位置上。Editor.insertEmbed 为VueEditor内置方法参数1为光标所在的位置。参数2为类型。参数3为图片的url地址
      Editor.insertEmbed(cursorLocation, 'image', res.data.url)
      resetUploader()
    },
    async fetch() {
      //根据id查询数据
      const res = await this.$http.get(`rest/blogs/${this.id}`)
      //问题: 为啥 this.model.name = res.data.name 不行？
      this.model = res.data
      console.log(this.model)
    },
    async save() {
      this.model.update_at = Date.now()
      if (this.id) {
        await this.$http.put(`rest/blogs/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/blogs', this.model)
      }

      this.$router.push('/articles/list')
      this.$message({
        type: 'success',
        message: '保存成功',
      })
    },
    async fetchCategories() {
      //根据id查询数据
      const res = await this.$http.get(`rest/categories`)
      //问题: 为啥 this.model.name = res.data.name 不行？
      this.categories = res.data
    },
  },
}
</script>

<style scoped>
.mt5 {
  margin-bottom: 5px;
}
</style>
